var graceChecker = require('@/common/graceChecker.js');
import pwInput from '@/components/input/PwInput'
export default {
	components: {
		pwInput
	},
	data() {
		return {
			phone: '',
			code: '',
			id_card_name: '',
			id_card: '',
			second: 90,
			showText: true,
			pay_paw: '',
			pay_pawa: '',
			agreeCheck: false
		};
	},
	onShow() {
		var user = uni.getStorageSync('uerInfo');
		if (user == '') {






			uni.redirectTo({
				url: '/pages/member/login?isBack=0'
			});

			return;
		}
	},
	onLoad() {},
	methods: {
		dump(url) {
			uni.navigateTo({
				url: url
			});
		},
		checkChange(e) {
			this.agreeCheck = e.detail.value;
		},
		sendCode() {
			//发送验证码
			var _this = this;
			if (_this.phone == '') {
				_this.$tools.toast('请填写正确的手机号码')
				return false;
			}

			//发送验证码
			_this.$request.common
				.sendCode({
					phone: _this.phone,
					type: 1
				})
				.then(data => {
					_this.$tools.loadingHide();
					if (data.status == 200) {
						_this.showText = false;
						var interval = setInterval(() => {
							let times = --_this.second;
							_this.second = times < 10 ? '0' + times : times; //小于10秒补 0
						}, 1000);
						setTimeout(() => {
							clearInterval(interval);
							_this.second = 90;
							_this.showText = true;
						}, 90000);
					} else {
						_this.$tools.toast(data.msg)
					}
				})
				.catch(err => {
					_this.$tools.loadingHide();
					//消息异常
					_this.$tools.toast('数据加载异常')
				});
		},
		btnClick(){
			var _this = this;
			if (!_this.agreeCheck) {
				_this.$tools.toast('请选阅读并同意用户协议说明')
				return false;
			}
			//定义表单规则
			var rule = [{
					name: 'phone',
					checkType: 'phoneno',
					errorMsg: '电话号码不正确'
				},
				{
					name: 'code',
					checkType: 'string',
					checkRule: '6,6',
					errorMsg: '验证码6个字符'
				},
				{
					name: 'id_card_name',
					checkType: 'string',
					checkRule: '1,100',
					errorMsg: '请输入正确的姓名'
				},
				{
					name: 'id_card',
					checkType: 'string',
					checkRule: '18,18',
					errorMsg: '请输入正确的身份证号码'
				},
				{
					name: 'pay_paw',
					checkType: 'string',
					checkRule: '6,20',
					errorMsg: '密码为6-20个字符'
				},
				{
					name: 'pay_pawa',
					checkType: 'same',
					checkRule: _this.pay_paw,
					errorMsg: '两次密码不相同'
				}
			];
			//进行表单检查
			var formData = {
				phone : this.phone,
				code : this.code,
				id_card_name : this.id_card_name,
				pay_paw : this.$refs['pay_paw'].getPassWord(),
				pay_pawa : this.$refs['pay_pawa'].getPassWord(),
			}
			
			var checkRes = graceChecker.check(formData, rule);
			if (checkRes) {
				_this.$tools.loading('数据提交中')
				_this.$request.user
					.setdatum(formData)
					.then(data => {
						_this.$tools.loadingHide();
						if (data.status == 1) {
							_this.$store.commit('setInformation');
			
							var user = uni.getStorageSync('uerInfo');
							var user_pay_state = user.user_pay_state; //支付信息
							if (user_pay_state != 1) {
								uni.navigateTo({
									url: '/pages/account/setCard?source=1'
								});
							} else {
								uni.switchTab({
									url: '/pages/member/center'
								});
							}
			
						} else {
							_this.$tools.toast(data.msg)
						}
					})
					.catch(err => {
						_this.$tools.loadingHide();
						//消息异常
						_this.$tools.toast('数据加载异常')
					});
			} else {
				_this.$tools.toast(graceChecker.error)
			}
		},
		formSubmit(e) {
			var _this = this;
			if (!_this.agreeCheck) {
				_this.$tools.toast('请选阅读并同意用户协议说明')
				return false;
			}
			//定义表单规则
			var rule = [{
					name: 'phone',
					checkType: 'phoneno',
					errorMsg: '电话号码不正确'
				},
				{
					name: 'code',
					checkType: 'string',
					checkRule: '6,6',
					errorMsg: '验证码6个字符'
				},
				{
					name: 'id_card_name',
					checkType: 'string',
					checkRule: '1,100',
					errorMsg: '请输入正确的姓名'
				},
				{
					name: 'id_card',
					checkType: 'string',
					checkRule: '18,18',
					errorMsg: '请输入正确的身份证号码'
				},
				{
					name: 'pay_paw',
					checkType: 'string',
					checkRule: '6,20',
					errorMsg: '密码为6-20个字符'
				},
				{
					name: 'pay_pawa',
					checkType: 'same',
					checkRule: _this.pay_paw,
					errorMsg: '两次密码不相同'
				}
			];
			//进行表单检查
			var formData = e.detail.value;

			var checkRes = graceChecker.check(formData, rule);
			if (checkRes) {
				_this.$tools.loading('数据提交中')
				_this.$request.user
					.setdatum(formData)
					.then(data => {
						_this.$tools.loadingHide();
						if (data.status == 1) {
							_this.$store.commit('setInformation');

							var user = uni.getStorageSync('uerInfo');
							var user_pay_state = user.user_pay_state; //支付信息
							if (user_pay_state != 1) {
								uni.navigateTo({
									url: '/pages/account/setCard?source=1'
								});
							} else {
								uni.switchTab({
									url: '/pages/member/center'
								});
							}

						} else {
							_this.$tools.toast(data.msg)
						}
					})
					.catch(err => {
						_this.$tools.loadingHide();
						//消息异常
						_this.$tools.toast('数据加载异常')
					});
			} else {
				_this.$tools.toast(graceChecker.error)
			}
		}
	}
};
